import React from "react";
import { Link } from "react-router-dom";

import "./app-header.css";

const AppHeader = () => {
  return (
    <header className="header">
      <div className="header__wrapper">
        <Link className="header__logo" to="/">
          AppCo
        </Link>
      </div>
    </header>
  );
};

export default AppHeader;
